import echarts from "echarts"; // eslint-disable-line
// require("echarts/theme/macarons"); // echarts theme

import { debounce } from "@/utils"; // eslint-disable-line

var selected = {};
export default {
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '400px'
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    chartData: {
      type: Array,
      required: true
    },
    ds: {
      type: Array,
      required: true
    }
  },
  data: function data() {
    return {
      chart: null,
      sidebarElm: null,
      checkedAccess: [0],
      names: {
        gmv: 'GMV',
        gmv_fee: '支付金额',
        gmv_fee_wx: '结算金额',
        gmv_fee_cc: '退款金额',
        pv_cc: '退款订单数',
        gmv_wx: '微信结算金额',
        commission: '手续费',
        pv_pay: '支付订单数',
        uv_pay: '支付用户数'
      }
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler: function handler(val) {
        this.setOptions(val);
      }
    },
    show: function show(v) {
      console.log(v);
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.chart = echarts.init(this.$refs.chart);
    Object.values(this.names).forEach(function (item) {
      selected[item] = false;
    });
    selected['GMV'] = true;
    this.chart.setOption({
      legend: {
        right: 50,
        selected: selected
      }
    });
    this.setOptions();

    if (this.autoResize) {
      this.__resizeHandler = debounce(function () {
        if (_this.chart) {
          _this.chart.resize();
        }
      }, 100);
      window.addEventListener('resize', this.__resizeHandler);
    } // 监听侧边栏的变化


    this.sidebarElm = document.getElementsByClassName('sidebar-container')[0];
    this.sidebarElm && this.sidebarElm.addEventListener('transitionend', this.sidebarResizeHandler);
  },
  beforeDestroy: function beforeDestroy() {
    if (!this.chart) {
      return;
    }

    if (this.autoResize) {
      window.removeEventListener('resize', this.__resizeHandler);
    }

    this.sidebarElm && this.sidebarElm.removeEventListener('transitionend', this.sidebarResizeHandler);
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    checkedWidth: function checkedWidth() {// const echarts = this.$refs.box
      // console.log(echarts.offsetWidth, 'qqqqqqqq')
      // console.log(echarts.offsetHeight, 'qqqqqqqq')
      // // //获取父元素宽高
      // var echartsWidth = echarts.offsetWidth
      // var echartsHeight = echarts.offsetHeight
      // // //获取图表元素
      // const myChart = this.$refs.chart
      // // //将父元素宽高赋值给图表
      // myChart.style.width = echartsWidth + 'px'
      // myChart.style.height = echartsHeight + 'px'
    },
    sidebarResizeHandler: function sidebarResizeHandler(e) {
      if (e.propertyName === 'width') {
        this.__resizeHandler();
      }
    },
    setOptions: function setOptions() {
      var _this2 = this;

      var series = [];
      this.checkedAccess.forEach(function (item) {
        if (_this2.chartData[item]) {
          for (var idx in _this2.chartData[item].value) {
            series.push({
              name: _this2.names[idx],
              itemStyle: {
                normal: {
                  lineStyle: {
                    width: 2
                  }
                }
              },
              smooth: true,
              type: 'line',
              data: _this2.chartData[item].value[idx],
              animationDuration: 2800,
              animationEasing: 'cubicInOut'
            });
          }
        }
      });
      this.chart.setOption({
        grid: {
          left: 20,
          right: 34,
          bottom: 20,
          top: 60,
          containLabel: true
        },
        xAxis: {
          data: this.ds,
          boundaryGap: false,
          axisTick: {
            show: false
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          }
        },
        series: series
      });
    },
    checked: function checked() {
      this.setOptions();
    }
  }
};