var moment = require('moment');

export default {
  props: {
    needexport: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      type: 'ds',
      syear: 0,
      eyear: 0,
      month: 0
    };
  },
  created: function created() {
    this.reset();
    this.commit();
  },
  methods: {
    handleDownload: function handleDownload() {
      this.$emit('handleDownload');
    },
    reset: function reset() {
      this.syear = moment().startOf('year').unix() * 1000;
      this.eyear = moment().endOf('year').unix() * 1000;
      this.month = moment().startOf('month').unix() * 1000;
    },
    change: function change(type) {
      this.type = type;
      this.reset();
      this.commit();
    },
    commit: function commit() {
      switch (this.type) {
        case 'yy':
        case 'qq':
          this.$emit('changeDate', {
            type: this.type,
            sts: this.syear / 1000,
            ets: this.eyear / 1000
          });
          break;

        case 'mm':
          this.$emit('changeDate', {
            type: this.type,
            sts: moment(this.eyear).startOf('year').unix(),
            ets: this.eyear / 1000
          });
          break;

        case 'ds':
          this.$emit('changeDate', {
            type: this.type,
            sts: this.month / 1000,
            ets: moment(this.month).endOf('month').unix()
          });
          break;
      }
    }
  }
};